.container {
    position: absolute;
    top: 80%;
    left: 55%;
    overflow: hidden;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.trail {
    height: 30px;
    width: 96px;
    background: -webkit-gradient(linear, left top, right top, from(#ff000000), to(red));
    background: -webkit-linear-gradient(left, #ff000000, red);
    background: -o-linear-gradient(left, #ff000000, red);
    background: linear-gradient(to right, #ff000000, red);
    left: 19%;
    top: 19.5%;
    position: absolute;
    z-index: -12;
    -webkit-animation: trail 1.7s linear infinite;
            animation: trail 1.7s linear infinite;
}

@-webkit-keyframes trail{
    0%{
        -webkit-transform: translate(-18%, -25%)rotate(0deg) scale(1);
                transform: translate(-18%, -25%)rotate(0deg) scale(1);
        opacity: 1;
    }
    25%{
        -webkit-transform: translate(-20%, -25%) rotate(1deg) scale(1.2);
                transform: translate(-20%, -25%) rotate(1deg) scale(1.2);
        opacity: 0.7;
    }
    50%{
        -webkit-transform: translate(-19%, -26%) rotate(0deg) scale(1);
                transform: translate(-19%, -26%) rotate(0deg) scale(1);
        opacity: 0.9;
    }
    75%{
        -webkit-transform: translate(-19%, -25%) rotate(-1deg) scale(1.2);
                transform: translate(-19%, -25%) rotate(-1deg) scale(1.2);
        opacity: 0.6;
    }
    100%{
        -webkit-transform: translate(-18%, -25%) rotate(0deg) scale(1);
                transform: translate(-18%, -25%) rotate(0deg) scale(1);
        opacity: 1;
    }
}

@keyframes trail{
    0%{
        -webkit-transform: translate(-18%, -25%)rotate(0deg) scale(1);
                transform: translate(-18%, -25%)rotate(0deg) scale(1);
        opacity: 1;
    }
    25%{
        -webkit-transform: translate(-20%, -25%) rotate(1deg) scale(1.2);
                transform: translate(-20%, -25%) rotate(1deg) scale(1.2);
        opacity: 0.7;
    }
    50%{
        -webkit-transform: translate(-19%, -26%) rotate(0deg) scale(1);
                transform: translate(-19%, -26%) rotate(0deg) scale(1);
        opacity: 0.9;
    }
    75%{
        -webkit-transform: translate(-19%, -25%) rotate(-1deg) scale(1.2);
                transform: translate(-19%, -25%) rotate(-1deg) scale(1.2);
        opacity: 0.6;
    }
    100%{
        -webkit-transform: translate(-18%, -25%) rotate(0deg) scale(1);
                transform: translate(-18%, -25%) rotate(0deg) scale(1);
        opacity: 1;
    }
}

svg {
    height: 480px;
    width: 680px;
    -webkit-transform: translate(-9%, -25%) rotate(138deg) scale(0.3);
        -ms-transform: translate(-9%, -25%) rotate(138deg) scale(0.3);
            transform: translate(-9%, -25%) rotate(138deg) scale(0.3);
    -webkit-animation: rocket 1.7s linear infinite;
            animation: rocket 1.7s linear infinite;
}

@-webkit-keyframes rocket {
    0% {
        -webkit-transform: translate(-18%, -25%) rotate(138deg) scale(0.3);
                transform: translate(-18%, -25%) rotate(138deg) scale(0.3);
    }
    25% {
        -webkit-transform: translate(-20%, -25%) rotate(139deg) scale(0.3);
                transform: translate(-20%, -25%) rotate(139deg) scale(0.3);
    }
    50% {
        -webkit-transform: translate(-19%, -26%) rotate(138deg) scale(0.3);
                transform: translate(-19%, -26%) rotate(138deg) scale(0.3);
    }
    75% {
        -webkit-transform: translate(-19%, -25%) rotate(137deg) scale(0.3);
                transform: translate(-19%, -25%) rotate(137deg) scale(0.3);
    }
    100% {
        -webkit-transform: translate(-18%, -25%) rotate(138deg) scale(0.3);
                transform: translate(-18%, -25%) rotate(138deg) scale(0.3);
    }
}

@keyframes rocket {
    0% {
        -webkit-transform: translate(-18%, -25%) rotate(138deg) scale(0.3);
                transform: translate(-18%, -25%) rotate(138deg) scale(0.3);
    }
    25% {
        -webkit-transform: translate(-20%, -25%) rotate(139deg) scale(0.3);
