        -webkit-transform: translate(-19%, -26%) rotate(138deg) scale(0.3);
                transform: translate(-19%, -26%) rotate(138deg) scale(0.3);
    }
    75% {
        -webkit-transform: translate(-19%, -25%) rotate(137deg) scale(0.3);
                transform: translate(-19%, -25%) rotate(137deg) scale(0.3);
    }
    100% {
        -webkit-transform: translate(-18%, -25%) rotate(138deg) scale(0.3);
                transform: translate(-18%, -25%) rotate(138deg) scale(0.3);
    }
}
@keyframes trail {
    0% {
        -webkit-transform: translate(-18%, -25%) rotate(0deg) scale(1);
                transform: translate(-18%, -25%) rotate(0deg) scale(1);
        opacity: 1;
    }
    25% {
        -webkit-transform: translate(-20%, -25%) rotate(1deg) scale(1.2);
                transform: translate(-20%, -25%) rotate(1deg) scale(1.2);
        opacity: 0.7;
    }
    50% {
        -webkit-transform: translate(-19%, -26%) rotate(0deg) scale(1);
                transform: translate(-19%, -26%) rotate(0deg) scale(1);
        opacity: 0.9;
    }
    75% {
        -webkit-transform: translate(-19%, -25%) rotate(-1deg) scale(1.2);
                transform: translate(-19%, -25%) rotate(-1deg) scale(1.2);
        opacity: 0.6;
    }
    100% {
        -webkit-transform: translate(-18%, -25%) rotate(0deg) scale(1);
                transform: translate(-18%, -25%) rotate(0deg) scale(1);
        opacity: 1;
    }
}


